header {
  height: 80px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid #a9adac;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

header h1 {
  margin: 0 1.5rem;
  font-size: 2rem;
  color: #e27a3f;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.button-link {
  margin: 0 1.5rem 0 0;
  text-decoration: none;
}

.temp-box {
  font-size: 0.5rem;
}

li {
  list-style: circle;
}

.dab {
  max-width: 100%;
  max-height: 100%;
  user-drag: none; 
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}


